
import { createSlice } from '@reduxjs/toolkit';
import { fetchArchivesAsync } from '../archive/archiveSlice';
import { useDispatch } from 'react-redux';

const initialState = {
    urlsInProgress: {
    },
    showProgressDialog: true,
    // need to monitor downloading,completed
    // removing completed and refresh page?, need use some animation
    // research on the animation in react
    // basicallly just need to detect 2 things
    // 1) adding of new url
    // 2) deleting of url(aka completion) and play the animation as required
}

export const progressdialogSlice = createSlice({
    name: 'progressdialog',
    initialState,
    reducers: {
        closeDialog: (state, action) => {
            state.showProgressDialog = false;
        },
        updateUrls: (state, action) => {
            // action.payload = action.payload || {};

            console.log('received update url actions');
            console.log(action.payload);
            // compare state with action payloadd for old and new
            // i have to keep track/statee of the progress too
            // dispatch new action for certain?
            let newState = { ...state }; //copy old state, is this immutable, or shallow copy only?
            newState.showProgressDialog = true;
            for (const [id, link] of Object.entries(action.payload || {})) {
                console.log("Checking from action payload: " + link);
                if (typeof state.urlsInProgress[id] === 'undefined') {// use property define or something?
                    // new
                    //dispatch added action?
                    //need to play some animation?
                    console.log('added new:' + link);
                    newState.urlsInProgress = {
                        ...newState.urlsInProgress,
                        [id]: {
                            link: link,
                            status: "downloading",
                        }
                    }
                }
            }
            //chaining it like this is a problem since the action performed above affects the bottom too
            for (const [id, { status, link }] of Object.entries(state.urlsInProgress)) {
                console.log("Checking from state: " + status + " " + link);
                if (!action.payload || typeof action.payload[id] === 'undefined') {
                    // completed items 
                    console.log('completed:' + link);
                    newState.urlsInProgress = {
                        ...newState.urlsInProgress,
                        [id]: {
                            ...newState.urlsInProgress[id],
                            status: "completed",
                        },
                        // Object.prototype.hasOwnProperty.call
                    }
                }
            }
            return newState;
        }
    }
})
export const { updateUrls, closeDialog } = progressdialogSlice.actions;
export const selectURLInProgress = (state) => state.progressdialog.urlsInProgress;
export const selectProgressDialogIsShowing = (state) => state.progressdialog.showProgressDialog;
export default progressdialogSlice.reducer;