import React, { useEffect, useState } from 'react';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { useDispatch, useSelector } from 'react-redux';
import { fetchArchivesAsync, selectArchives, selectStatus } from './archiveSlice';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Pagination from '@material-ui/lab/Pagination';
import Link from '@material-ui/core/Link';
import { useSpring, animated } from 'react-spring';
import diskimg from '../../resources/disk.svg';


const EACH_PAGE_COUNT = 5;

const useStyles = makeStyles((theme) => ({
    centering: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        marginTop: theme.spacing(4),
    },
    archivecontainer: {
        marginTop: theme.spacing(8),
    },
    firstArchive: {
        marginTop: theme.spacing(4),
    },
    gridcontainer: {
        backgroundColor: theme.palette.background.paper,
        display: "grid",
        gridTemplateColumns: "2fr 3fr 4fr",
        maxWidth: "100%",
        marginTop: theme.spacing(3),
    },
    imgofpage: {
        width: "max-content",
        overflowY: "hidden",
        height: "100%",
        maxHeight: "100%",
        maxWidth: "100%",
        verticalAlign: "top",
        objectFit: "cover",
        // objectFit: "contain",
        // objectFit: "scale-down",
        // https://www.w3schools.com/css/css3_object-fit.asp
        objectPosition: "0% 0%",
    },
    archiveTitle: {
        fontFamily: "'Train One', cursive",
    },
    paper: {
        height: "100%",
    },
    fillheight: {
        // height: "100%",
        height: "200px",
    }
}));

function SingleArchive(props) {
    console.log('props withing single archive');
    console.log(props);
    const classes = useStyles();

    return (
        <div className={props.idx == 0 ? classes.firstArchive : classes.archivecontainer} >
            <Typography variant="h5" gutterBottom>
                {props.title}
            </Typography>
            <div className={classes.gridcontainer}>
                <Container className={classes.fillheight}>
                    <Paper elevation={3} className={classes.paper}>
                        <a href={props.img0B} target="_blank" rel="noopener noreferrer nofollow">
                            <img className={classes.imgofpage} src={props.img0} />
                        </a>
                    </Paper>
                </Container>
                <Container className={classes.fillheight}>
                    <Paper elevation={3} className={classes.paper}>
                        <a href={props.img1B} target="_blank" rel="noopener noreferrer nofollow">
                            <img className={classes.imgofpage} src={props.img1} />
                        </a>
                    </Paper>
                </Container>
                <Container className={classes.fillheight}>
                    <Paper elevation={3} className={classes.paper}>
                        <a href={props.img2B} target="_blank" rel="noopener noreferrer nofollow">
                            <img className={classes.imgofpage} src={props.img2} />
                        </a>
                    </Paper>
                </Container>
            </div>
            <Container style={{ paddingTop: "16px", display: "flex", justifyContent: "space-between" }}>
                <Link href={props.url} target="_blank" rel="noopener noreferrer nofollow" >
                    Visit Link
              </Link>
                <Link href="#" >
                    Set revision alert
              </Link>
            </Container>
        </div >
    );
}

export default function Archive() {
    const dispatch = useDispatch();
    // select the archives
    let archives = useSelector(selectArchives);
    let status = useSelector(selectStatus);
    console.log('reverse');
    // window.aaa = archives;
    let reverseArchives = [...archives].reverse();
    let [page, setPage] = useState(1);

    const classes = useStyles();
    useEffect(() => {
        console.log('running archive mounting stuff');
        //dispatch
        dispatch(fetchArchivesAsync());
    }, []);

    function handlePageChange(e, v) {
        console.log(v);
        setPage(v);
    }

    const diskanimation = useSpring({
        loop: true,
        to: [
            { opacity: 1, color: '#ffaaee' },
            { opacity: 0, color: 'rgb(14,26,19)' },
        ],
        from: {
            opacity: 0, color: 'red',
        },
        config: {
            duration: 3000,
        }

    })

    return status !== "loading" && (
        <Container maxWidth="md">

            <div style={{ position: "relative" }}>
                {/* <animated.div
                    style={diskanimation}
                >
                    <img src={diskimg} style={{
                        position: "absolute",
                        right: "80vw",
                        width: "32px",
                        height: "32px",
                        transform: "rotate(45deg)",

                    }} />
                </animated.div> */}
                <Typography variant="h3" className={classes.archiveTitle} gutterBottom>
                    ARCHIVES
            </Typography>
            </div>
            {reverseArchives.filter(
                (x, idx) => idx >= (page - 1) * EACH_PAGE_COUNT
                    && idx < page * EACH_PAGE_COUNT).map(
                        (a, idx) => (<SingleArchive idx={idx} key={a.id} {...a} />)
                    )}
            <div className={classes.centering}>
                <Pagination page={page} onChange={handlePageChange} count={Math.ceil(reverseArchives.length / 5)} color="secondary" />
            </div>
        </Container>
    );
}