import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import CssBaseline from '@material-ui/core/CssBaseline';
import './App.css';
import Archive from './features/archive/Archive';
import Hero from './component/Hero.js';
import Footer from './component/Footer.js';
import ProgressDialog from './features/progressdialog/ProgressDialog.js';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { makeStyles } from '@material-ui/core/styles';

import { selectSBOpen, closeSB, selectSBMsg, selectSBType } from './features/submissionbar/submissionbarSlice';

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}



function App() {
  const isSBOpen = useSelector(selectSBOpen);
  const SBMsg = useSelector(selectSBMsg);
  const SBMsgType = useSelector(selectSBType);
  const dispatch = useDispatch();

  const handleClose = (event, reason) => {

    if (reason === 'clickaway') {
      return;
    }
    console.log('trying to close');
    // setOpen(false);
    // console.log('type of dispatch: ' + typeof dispatch);
    // console.log('type of closeSB: ' + typeof closeSB);
    // console.log('closeSB: ' + closeSB);
    dispatch(closeSB());
  };

  return (
    <>
      <CssBaseline />
      <Hero />
      <Archive />
      <Snackbar open={isSBOpen} autoHideDuration={1200} onClose={handleClose}>
        <Alert onClose={handleClose} severity={SBMsgType}>
          {SBMsg}
        </Alert>
      </Snackbar>
      <ProgressDialog />
      <Footer />
    </>
  );
}

export default App;
