import { put, takeEvery, takeLatest, all, select, take } from 'redux-saga/effects';
import { updateUrls } from './features/progressdialog/progressdialogSlice';
import { fetchArchivesAsync } from './features/archive/archiveSlice';

function* queueChangesAsync(action) {
    let state = yield select();
    console.log('queue changes async curState');
    console.log(state);
    console.log('action:');
    console.log(action);
    yield put(updateUrls(action.payload));
    yield put(fetchArchivesAsync());
}
function* watchQueueChangesAsync() {
    yield takeLatest('QUEUE_CHANGE', queueChangesAsync);
}
export default function* rootSaga() {
    yield all([
        watchQueueChangesAsync(),
    ]);
}