import React, { useEffect, useState } from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import SubmissionBar from '../features/submissionbar/SubmissionBar';
import { db } from '../services/firebase';
import diskimg from '../resources/disk.svg';
import { useSpring, animated } from 'react-spring';
import Slide from '@material-ui/core/Slide';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import CircularProgress from '@material-ui/core/CircularProgress';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles({
    title: {
        fontFamily: "'Train One', cursive",
        // color: "white",
    },
    heroBackground: {
        width: "100%",
        margin: 0,
        textAlign: "center",
        height: "500px",
        paddingTop: "120px",
        boxSizing: "border-box",
        height: "100vh",
        // background: "gray url(https://www.toptal.com/designers/subtlepatterns/patterns/moroccan-flower-dark.png) repeat 0 0",
        // animation: "slide 20s linear infinite",
    },
    greenIndicator: {
        background: "#28B62C",
        display: "inline-block",
        width: "1em",
        height: "1em",
        borderRadius: "50%",
        // -webkit-animation: pulse-animation 2s infinite linear;
        animation: `$pulse 2s infinite linear`,
    },
    redIndicator: {
        background: "red",
        display: "inline-block",
        width: "1em",
        height: "1em",
        borderRadius: "50%",

    },
    "@keyframes pulse": {
        "0%": { transform: "scale(1)" },
        "25%": { transform: "scale(1)" },
        "50%": { transform: "scale(1.2)" },
        "75%": { transform: "scale(1)" },
        "100%": { transform: "scale(1)" },
    }
});

export default function Hero() {
    const classes = useStyles();
    // need to dispatch status and selector for status 
    const [status, setStatus] = useState("online");

    const diskanimation = useSpring({
        loop: true,
        to: [
            { opacity: 1, color: '#ffaaee' },
            { opacity: 0, color: 'rgb(14,26,19)' },
        ],
        from: {
            opacity: 0, color: 'blue',
        },
        config: {
            duration: 3000,
        }

    })

    useEffect(() => {
        var ref = db.ref("status");
        ref.on('value', (snapshot) => {
            console.log('status: ' + snapshot.val());
            setStatus(snapshot.val());
        }, (errorObject) => {
            console.log('The read failed: ' + errorObject.name);
        });
    }, []);


    return (
        <>
            <div style={{ display: "flex", justifyContent: "flex-end", alignItems: "center", margin: "16px" }}>
                {status === "online"
                    ? (<span className={classes.greenIndicator}></span>)
                    : (<span className={classes.redIndicator}></span>)}
                <Typography className={classes.title} style={{ marginLeft: "8px" }} variant="subtitle2" gutterBottom>
                    {status}
                </Typography>
            </div>
            <div className={classes.heroBackground}>
                <div style={{ position: "relative" }}>
                    <animated.div
                        style={diskanimation}
                    >
                        <img src={diskimg} style={{
                            position: "absolute",
                            right: "50vw",
                            width: "32px",
                            height: "32px",
                            transform: "rotate(45deg)",

                        }} />
                    </animated.div>
                    <Typography
                        className={classes.title}
                        // component="span"
                        variant="h2"
                        gutterBottom>
                        Page Saver
                    </Typography>
                </div>
                <SubmissionBar />
            </div >
            <Dialog
                open={status === "offline"}
                TransitionComponent={Transition}
                keepMounted
                aria-labelledby="alert-dialog-slide-title"
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogTitle id="alert-dialog-slide-title">{"Server is currently offline"}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-slide-description">
                        The server is currently offline.<br />
                        Please wait for server to boot...
                        <div style={{
                            display: "flex",
                            justifyContent: "center"
                        }}>
                            <CircularProgress color="secondary" />
                        </div>
                    </DialogContentText>
                </DialogContent>
            </Dialog>
        </>
    );
}