import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';
import TextField from '@material-ui/core/TextField';
import styles from './SubmissionBar.module.css';
import { showInvalidURLMsg, submitURLAsync } from './submissionbarSlice';
import validUrl from 'valid-url';

const useStyles = makeStyles((theme) => ({
    container: {
        marginTop: theme.spacing(8),
    },
    savebutton: {
        minWidth: "150px",
        alignSelf: "flex-end",
        height: "56px",
        [theme.breakpoints.up('md')]: {
            flexDirection: "row",
            minwidth: "0px",
            marginLeft: "32px",
        },
    },
    input: {
        width: "80%",
    },
    root: {
        margin: "auto",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        '& > *': {
            margin: theme.spacing(2),
        },
        flexDirection: "column",
        [theme.breakpoints.up('sm')]: {
            flexDirection: "row",
            maxWidth: "500px",
        },
    },
}));


export default function SubmissionBar() {
    // const [submissiontext, setSubmissionText] = useState('');
    // we use uncontrolled ref
    // need media query later for the submission button below if size is too small
    const dispatch = useDispatch();;

    let textRef = React.createRef();
    let submitURL = (e) => {
        e.preventDefault();
        let url = textRef.current.value;
        console.log(url);
        if (validUrl.isUri(url)) {
            console.log('Looks like an URI');
            dispatch(submitURLAsync(url));
            textRef.current.value = '';
        } else {
            console.log('Not a URI');
            dispatch(showInvalidURLMsg());
        }

    };

    const classes = useStyles();
    return (
        <div className={classes.container}>
            <form className={classes.root} onSubmit={submitURL} noValidate autoComplete="off">
                <TextField
                    inputRef={textRef}
                    className={classes.input}
                    label="Enter url of page to save"
                />
                <Button
                    className={classes.savebutton}
                    startIcon={<SaveIcon />}
                    variant="contained"
                    color="primary"
                    onClick={submitURL}
                >Save</Button>
            </form>
        </div>
    )
}