import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { submitURL } from './submissionbarAPI.js';

const initialState = {
    status: 'idle',
    snackbarOpen: false,
    snackbarMsg: '',
    snackbarType: 'error',
}

export const submitURLAsync = createAsyncThunk(
    'submissionbar/submitURL',
    async (url) => {
        console.log(`trying to submit async ${url}`);
        const response = await submitURL(url);
        return response.data;
    }
)


export const selectSBOpen = (state) => state.submissionbar.snackbarOpen;
export const selectSBMsg = (state) => state.submissionbar.snackbarMsg;
export const selectSBType = (state) => state.submissionbar.snackbarType;

export const submissionbarSlice = createSlice({
    name: 'submissionbar',
    initialState,
    reducers: {
        closeSB: (state) => {
            state.snackbarOpen = false;
        },
        showInvalidURLMsg: (state) => {
            state.snackbarOpen = true;
            state.snackbarMsg = 'Url is invalid';
            state.snackbarType = 'error';
        },
    },
    extraReducers: (builder) => {
        builder
            .addCase(submitURLAsync.pending, (state) => {
                state.status = 'submitting';
            })
            .addCase(submitURLAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                // state.value += action.payload;
                state.snackbarOpen = true;
                // clear the 
                state.snackbarMsg = 'Url submitted';
                state.snackbarType = 'success';
            });

    },
});

export const { closeSB, showInvalidURLMsg } = submissionbarSlice.actions;

export default submissionbarSlice.reducer;