import axios from 'axios';
import { localURL, productionURL } from '../../constants.js'
export function fetchArchives() {
    let endpoint = '';
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
        endpoint = localURL + '/pagesave';
        // dev code
    } else {
        // production code
        endpoint = productionURL + '/pagesave';
    }
    return new Promise((resolve, reject) => {
        axios.get(endpoint, {
        })
            .then(function (response) {
                console.log(response);
                resolve(response);
            })
            .catch(function (error) {
                console.log(error);
                reject(error);
            });

    });
}