import { configureStore, getDefaultMiddleware } from '@reduxjs/toolkit';
import counterReducer from '../features/counter/counterSlice';
import submissionBarReducer from '../features/submissionbar/submissionbarSlice';
import archiveReducer from '../features/archive/archiveSlice';
import progressdialogReducer from '../features/progressdialog/progressdialogSlice';
import createSagaMiddleware from 'redux-saga';
import rootSaga from '../sagas';

const sagaMiddleware = createSagaMiddleware()


export const store = configureStore({
  reducer: {
    counter: counterReducer,
    submissionbar: submissionBarReducer,
    archive: archiveReducer,
    progressdialog: progressdialogReducer,
  },
  middleware: (getDefaultMiddleware) =>
    [...getDefaultMiddleware(), sagaMiddleware],


});

sagaMiddleware.run(rootSaga)
