import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { db } from '../../services/firebase.js';
import { selectURLInProgress, selectProgressDialogIsShowing, updateUrls, closeDialog } from './progressdialogSlice';
import { makeStyles } from '@material-ui/core/styles';
import AutorenewIcon from '@material-ui/icons/Autorenew';
import { Typography } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import { shadows } from '@material-ui/system';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import DescriptionRoundedIcon from '@material-ui/icons/DescriptionRounded';
import CloseIcon from '@material-ui/icons/Close';
import { ellipsis } from '../../helper/util';
import LinearProgress from '@material-ui/core/LinearProgress';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Hidden from '@material-ui/core/Hidden';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Checkmark } from 'react-checkmark';
import { useTheme } from '@material-ui/core/styles';
import { borders } from '@material-ui/system';
import IconButton from '@material-ui/core/IconButton';
import { useSpring, animated } from 'react-spring';
import Slide from '@material-ui/core/Slide';

const ELLIPSIS_LIMIT = 30;
const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: "90vw",
        position: "fixed",
        bottom: "0px",
        right: "0px",
        margin: "16px",
        paddingBottom: '0px',
        // background: "pink",
        // borderRadius: "50px",
    },
    header: {
        display: "flex",
        background: "white",
        borderRadius: "10px 10px 0px 0px",
        alignItems: "center",
        // border: "2px solid red",
    },
    headericon: {

    },
    acsummary: {
        padding: "0px",
    },
    acsummarycontent: {
        padding: "0px",
        margin: "0px",
        '&$expanded': {
            margin: "0px",
            padding: "0px",
        },
    },
    lastchild: {
        '&:last-child': {
            paddingBottom: '8px',
        },
    },
    checkmark: {
        // display: "flex",
        // justifyContent: "flex-end",
        marginLeft: "auto",
    },
    urlcontainer: {
        display: "flex",
        padding: theme.spacing(1) + "px " + theme.spacing(1) + "px",
        width: 'inherit',

        alignItems: "center",
    },
    urlcontainercompleted: {
        display: "flex",
        padding: theme.spacing(1) + "px " + theme.spacing(3) + "px",
        color: theme.palette.primary.dark,
        // background: theme.palette.primary.light,
        alignItems: "center",

    },
    overrideaccordionshadowandmargin: {
        boxShadow: "none",
        border: "none",
        margin: '0',
        '&$expanded': {
            margin: "0",
        },
    },
    expanded: {
        // this is required to be blank
    },
    urls: {
        marginLeft: theme.spacing(2),
        wordBreak: "break-word",
        whiteSpace: "normal",
        textOverflow: "ellipsis",

    },
    processingQueue: {
        [theme.breakpoints.down('sm')]: {
            fontSize: theme.typography.body1.fontSize,
        },
    }

}));
export default function ProgressDialog(props) {
    const existingURLs = useSelector(selectURLInProgress);
    const isProgressDialogVisible = useSelector(selectProgressDialogIsShowing);

    const dispatch = useDispatch();

    const classes = useStyles();
    const { anchorEl } = props;
    useEffect(() => {
        // Initialize Firebase
        var queueRef = db.ref('queue/');
        queueRef.on('value', (snapshot) => {
            const data = snapshot.val();
            console.log("queue changes");
            console.log(data);

            // updateStarCount(postElement, data);
            // dispatch(updateUrls(data));
            dispatch({
                type: 'QUEUE_CHANGE',
                payload: data,
            });
        });

    }, []);

    const [expanded, setExpanded] = React.useState(false);

    const theme = useTheme();

    const handleChange = () => {
        setExpanded(!expanded);
    };
    console.log('existingurls');
    window.exu = existingURLs;
    console.log(existingURLs);

    const loopingStyle = useSpring({
        loop: true,
        from: { rotateZ: 0 },
        to: { rotateZ: 360 },
    })
    return (
        <Slide direction="up" in={Object.values(existingURLs).length > 0 && isProgressDialogVisible} mountOnEnter unmountOnExit>
            <Box
                borderRadius="20px"
                boxShadow={3}
                // width="300px"
                // height="300px"
                m={0}
                style={{ paddingRight: "8px", paddingLeft: "8px", paddingTop: "8px", paddingBottom: "8px", }}
                // m and p values are important
                bgcolor="background.paper" className={classes.root}>
                <Accordion
                    expanded={!expanded}
                    onChange={handleChange}
                    classes={{
                        root: classes.overrideaccordionshadowandmargin,
                        expanded: classes.expanded,
                    }}
                >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                        p={0}
                        m={0}
                        style={{
                            paddingRight: "40px",
                            // borderBottom: "solid rgba(0, 0, 0, 0.27) 1px",
                            // boxShadow: "rgb(0 0 0 / 20%) 0px 3px 1px -2px, rgb(0 0 0 / 14%) 0px 2px 2px 0px, rgb(0 0 0 / 12%) 0px 1px 5px 0px",
                        }}
                        classes={{
                            root: classes.acsummary,
                            content: classes.acsummarycontent,
                            expanded: classes.expanded,
                        }}
                    >
                        <div className={classes.header}>
                            <animated.div style={loopingStyle}>
                                <AutorenewIcon style={{ margin: "8px" }} color="primary" fontSize="large" />
                            </animated.div>
                            <div>
                                <Typography variant="h6" gutterBottom={false} className={classes.processingQueue}>
                                    Processing
                            </Typography>
                                {!expanded && (
                                    <Typography variant="caption">
                                        {Object.values(existingURLs).filter(a => a.status === 'downloading').length} page saving in progress...
                                    </Typography>
                                )}
                            </div>
                        </div>
                        <IconButton
                            style={{ position: "absolute", right: "0px", top: "8px" }}
                            aria-label="close"
                            onClick={() => dispatch(closeDialog())}
                        // color="primary"
                        >
                            <CloseIcon fontSize="small"></CloseIcon>
                        </IconButton>
                    </AccordionSummary>
                    <AccordionDetails style={{ margin: 0, padding: 0 }}>
                        <div className={classes.body}>
                            {Object.entries(existingURLs).map(([id, url]) => {
                                if (url.status === 'downloading') {
                                    return (
                                        <div key={id} className={classes.lastchild}>
                                            <div className={classes.urlcontainer}>
                                                <DescriptionOutlinedIcon fontSize="small" color="disabled" />
                                                <Typography variant="body2" className={classes.urls}>
                                                    Saving {ellipsis(url.link, ELLIPSIS_LIMIT)}
                                                </Typography>
                                            </div>
                                            <Container>
                                                <LinearProgress color="primary" />
                                            </Container>

                                        </div>);

                                } else {
                                    return (
                                        <div key={id} className={classes.urlcontainercompleted}>
                                            <DescriptionRoundedIcon fontSize="small" />
                                            <Typography className={classes.urls} >
                                                {ellipsis(url.link, ELLIPSIS_LIMIT)}
                                                {/* {url.link} */}
                                            </Typography>
                                            <div className={classes.checkmark}>
                                                <Checkmark size='medium' color={theme.palette.primary.main} />
                                            </div>
                                        </div>);
                                };
                            })
                            }
                        </div>
                    </AccordionDetails>
                </Accordion>
            </Box >
        </Slide>
    )
}